import React, { ReactElement, useState, useEffect } from "react";
import {
  Modal,
  Spinner,
  Button,
  Dropdown
} from "react-bootstrap";
import { Auth } from "aws-amplify";
import Timer from "./Timer/Timer";
import EndLab from "./EndLab/EndLab";
import Finish from "./Finish/Finish";
import Scenario from "./Scenario/Scenario";
import PreLab from "./PreLab/PreLab";
import Home from "./Home/Home";
import SignUp from "./SignUp/SignUp";
import SignIn from "./SignIn/SignIn";
import ConfirmSignUp from "./ConfirmSignUp/ConfirmSignUp";
import { UserDetails, UserDetailsDefault } from "./types";
import BackendAPI from "./api";
import "./App.scss";

const App = () : ReactElement => {
  const [view, setView] = useState("SignIn");
  const [userDetails, setUserDetails] = useState<UserDetails>(UserDetailsDefault);
  const [modalShow, setModalShow] = useState(false);
  const [confirmDisabled, setConfirmDisabled] = useState(false);

  useEffect(() => {
    Auth.currentSession()
      .then((response) => {
        console.log(response);
        setUserDetails({
          ...userDetails,
          email: response.getAccessToken().payload.username,
          signIn: true
        });
        setView("Home");
      })
      .catch((error) => {
        console.log(error);
      });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const modalHandleClose = () => {
    setModalShow(false);
  };

  const signOut = () => {
    Auth.signOut()
      .then(() => {
        setUserDetails(UserDetailsDefault);
        setView("SignIn");
      });
  };

  const endLabHandler = () => {
    setConfirmDisabled(true);
    BackendAPI.TearDownEnvironment()
      .then(() => {
        setUserDetails({
          ...userDetails,
          currentState: "terminating"
        });
        modalHandleClose();
        setView("EndLab");
      });
  };

  const endLabLabel = userDetails.currentState === "terminating" ? "Ending Lab" : "End Lab";

  return (
    <div className="app">
      <Modal show={modalShow} onHide={modalHandleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Are you sure?</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          This action will revoke your access to the lab, and tear down the lab environment. It will take a while before you can redo the lab.
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={modalHandleClose}>
            Close
          </Button>
          <Button variant="danger" onClick={endLabHandler} disabled={confirmDisabled}>
            {
              confirmDisabled
              && (
              <Spinner
                as="span"
                animation="border"
                size="sm"
                role="status"
                aria-hidden="true"
              />
              )
            }
            {" "}
            Confirm
          </Button>
        </Modal.Footer>
      </Modal>
      <div className="app-header">
        {
          userDetails.accountId
          && (
            <>
              <div className="left-header">
                <Button
                  onClick={() => setModalShow(true)}
                  variant="danger"
                  disabled={userDetails.currentState === "terminating"}
                >
                  {endLabLabel}
                </Button>
                {
                  userDetails.currentState !== "terminating"
                  && (
                  <Timer
                    setView={setView}
                    userDetails={userDetails}
                    setUserDetails={setUserDetails}
                    createDate={userDetails.createDate}
                  />
                  )
                }
              </div>
              <span className="info-link">
                Lab URL :
                {" "}
              </span>
              <Button
                variant="link"
                className="link"
                href={`https://${userDetails.accountId}.signin.aws.amazon.com/console`}
                target="_blank"
              >
                https://
                {userDetails.accountId}
                .signin.aws.amazon.com/console
              </Button>
            </>
          )
        }
        {
          userDetails.signIn
          && (
          <>
            <span className="info-link">
              Account :
              {" "}
            </span>
            <Dropdown>
              <Dropdown.Toggle className="link">
                {userDetails.email}
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item onClick={signOut}>Sign Out</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </>
          )
        }
      </div>
      <div className="app-body">
        {view === "Home" && <Home setView={setView} userDetails={userDetails} setUserDetails={setUserDetails} />}
        {view === "SignIn" && <SignIn setView={setView} userDetails={userDetails} setUserDetails={setUserDetails} />}
        {view === "SignUp" && <SignUp setView={setView} userDetails={userDetails} setUserDetails={setUserDetails} />}
        {view === "ConfirmSignUp" && <ConfirmSignUp setView={setView} email={userDetails.email} />}
        {view === "PreLab" && <PreLab setView={setView} />}
        {
          view === "Scenario"
          && <Scenario setView={setView} accountId={userDetails.accountId} progress={userDetails.progress} />
        }
        {view === "Finish" && <Finish />}
        {view === "EndLab" && <EndLab />}
      </div>
    </div>
  );
};

export default App;
